止那日的大屠杀，绝望地在废墟里搜寻着幸存者，冒着生命危险拯救了一名叫做Sarah的小女孩（日后她成为了拥有抽骨异能的Morlocks领袖骨后Marrow")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br')])}
